.card-form {
  background: radial-gradient(circle at 50% 50%,#0094ff87, rgba(255,193,206,0.78));
  border-radius: 10px;
  padding: 20px;
  width: 450px;
  max-width: 100%; /* Ensure card width does not exceed viewport width */
  margin: 0 auto; /* Center the card horizontally */
  display: flex;
  flex-direction: column;
  border: none;
}

.title {
  font-size: 24px;
  font-weight: 600;
  text-align: center;
}

.form {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

.group {
  position: relative;
}

.form .group label {
  font-size: 14px;
  color: rgb(99, 102, 102);
  position: absolute;
  top: -10px;
  left: 10px;
  background-color: #fff;
  transition: all .3s ease;
}

.form .group input,
.form .group textarea {
  padding: 10px;
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
  outline: 0;
  width: 100%;
  background-color: transparent;
}
.input-placeholder {
  color: black;
}

.form .group input:placeholder-shown + label, .form .group textarea:placeholder-shown + label {
  top: 10px;
  background-color: transparent;
}

.form .group input:focus,
.form .group textarea:focus {
  border-color: #3366cc;
}

.form .group input:focus + label, .form .group textarea:focus + label {
  top: -10px;
  left: 10px;
  background-color: #fff;
  color: #3366cc;
  font-weight: 600;
  font-size: 14px;
}

.form .group textarea {
  resize: none;
  height: 100px;
}

.form button {
  background-color: #1f8ce0;
  color: #fff;
  border: none;
  border-radius: 0.5rem;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
  margin: 0 auto; /* Center the button horizontally */
  display: block; /* Ensure button takes full width */
}

.form button:hover {
  color: black;
}
